@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,600,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,html,body{padding:0;margin:0;font-family:"Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif}.container{width:95%;max-width:1200px;margin:0 auto}.container .wrapper{padding:0 48px}@media (max-width: 680px){.container .wrapper{padding:0 20px}}.container .wrapper .home{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100%;width:100%}.container .wrapper .home p{font-size:0.75rem;letter-spacing:0.3rem;text-transform:uppercase;font:500}.container .wrapper .home h5{margin-top:260px;font-size:1.4rem;font-weight:600}@media (max-width: 680px){.container .wrapper .home h5{font-size:1rem}}header{height:100px;position:fixed;width:100%;z-index:99}header .inner-header{position:relative;z-index:10;height:100px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}header .inner-header .logo a{font-weight:700;text-decoration:none;color:#191919}header .inner-header .menu button{border:none;background:none;outline:none;cursor:pointer;font-size:0.8rem;color:#191919;font-weight:600}header .hamburger-menu{display:none;z-index:9;top:0;bottom:0;left:0;right:0;position:fixed;height:100%;width:100%}header .hamburger-menu .menu-secondary-background-color{background-color:#191919;z-index:-1;top:0;bottom:0;left:0;right:0;position:fixed;height:100%;width:100%}header .hamburger-menu .menu-layer{position:relative;height:100%;background:#e20001;overflow:hidden}header .hamburger-menu .menu-layer .menu-city-background{top:0;bottom:0;left:0;right:0;position:absolute;height:100%;width:100%;opacity:0;background-size:cover;background-repeat:no-repeat;-webkit-animation:cameraPan 30s infinite alternate;animation:cameraPan 30s infinite alternate}header .hamburger-menu .menu-layer .wrapper{position:relative}header .hamburger-menu .menu-layer .wrapper .menu-links{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;position:relative;top:140px}@media (max-width: 680px){header .hamburger-menu .menu-layer .wrapper .menu-links{-webkit-flex-direction:column;flex-direction:column}}header .hamburger-menu .menu-layer .wrapper .menu-links nav{display:block}header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li{list-style:none;font-size:6rem;font-weight:700;cursor:pointer;height:135px;overflow:hidden;position:relative;width:700px}@media (max-width: 680px){header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li{font-size:2rem;height:80px;width:258px}}header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a{text-decoration:none;position:absolute;color:#fff;transition:0.25s linear}header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a::after{position:absolute;height:8px;width:100%;-webkit-transform:scaleX(0);transform:scaleX(0);background:white;-webkit-transform-origin:bottom left;transform-origin:bottom left;content:"";top:50%;left:0;transition:0.3s linear;border-radius:2px}header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a:hover{color:#191919}header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a:hover::after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:bottom right;transform-origin:bottom right}header .hamburger-menu .menu-layer .wrapper .menu-links .info{color:white;width:300px}@media (max-width: 680px){header .hamburger-menu .menu-layer .wrapper .menu-links .info{display:none}}header .hamburger-menu .menu-layer .wrapper .menu-links .info h3{font-size:1.2rem;margin:8px auto}header .hamburger-menu .menu-layer .wrapper .menu-links .info p{margin:0 auto;font-size:0.8rem;line-height:1.65}header .hamburger-menu .menu-layer .wrapper .locations{position:absolute;display:-webkit-flex;display:flex;bottom:-80px;color:#fff;margin-top:16px;font-size:0.8rem;font-weight:600}@media (max-width: 680px){header .hamburger-menu .menu-layer .wrapper .locations{bottom:-50%}header .hamburger-menu .menu-layer .wrapper .locations .locationText{display:none}}header .hamburger-menu .menu-layer .wrapper .locations span{cursor:pointer;font:400;margin:0 32px;transition:0.3s ease-in-out}header .hamburger-menu .menu-layer .wrapper .locations span:first-child{margin-left:64px}header .hamburger-menu .menu-layer .wrapper .locations span:hover{background:#191919;padding:8px 24px;border-radius:4px}@media (max-width: 680px){header .hamburger-menu .menu-layer .wrapper .locations span{margin:0 5px}}@-webkit-keyframes cameraPan{0%{background-position:0% 0%}25%{background-position:40% 10%}50%{background-position:0% 10%}75%{background-position:10% 40%}100%{background-position:40% 10%}}@keyframes cameraPan{0%{background-position:0% 0%}25%{background-position:40% 10%}50%{background-position:0% 10%}75%{background-position:10% 40%}100%{background-position:40% 10%}}

.Appscroll{position:fixed;top:0;left:0;height:100%;width:100%;overflow:hidden}.Appscroll h2{text-align:center;font-size:8rem;margin:40px auto}@media (max-width: 480px){.Appscroll h2{font-size:6rem}}.Appscroll h2 .outline{-webkit-text-stroke:1px black;-webkit-text-fill-color:white}.Appscroll .scroll{padding:2vh 0;pointer-events:none}.Appscroll .img-container{max-width:400px;height:320px;overflow:hidden;position:relative;margin:100px auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.Appscroll .img-container:last-child{margin-bottom:200px}@media (max-width: 480px){.Appscroll .img-container{width:95%}}.Appscroll .img-container img{width:100%;position:absolute}

